.site-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  z-index: 2;
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  margin-top: 65px;

  @media (min-width: $tablet-width) {
    display: flex;
    margin: 0;
    padding: 0;
    order: 1;
    position: static;
    flex-direction: row;
  }
}

.site-list__item {
  position: relative;
  box-shadow: inset 0 -1px 0 0 #e5e5e5;

  @media (min-width: $tablet-width) {
    box-shadow: none;
  }
}

.site-list__item:first-child {
  box-shadow: inset 0 -1px 0 0 #e5e5e5, inset 0 1px 0 0 #e5e5e5;

  @media (min-width: $tablet-width) {
    box-shadow: none;
  }
}

.site-list__item--link {
  color: black;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  padding-left: 103px;
  padding-top: 25px;
  padding-bottom: 24px;
  display: block;

  @media (min-width: $tablet-width) {
    height: 100%;
    padding: 15px 15px 20px 15px;
  }
}

.site-list__item--search {
  @media (min-width: $tablet-width) {
    font-size: 0;
  }

  @media (min-width: $tablet-width) {
    left: -115px;
    top: 19px;
  }
}

.site-list--user-nav {
  @media (min-width: $tablet-width) {
    justify-content: flex-end;
    flex-grow: 1;
  }

  @media (min-width: $desktop-width) {
    flex: 0.7;
    order: 3;
  }
}

.site-list--nav {
  padding-top: 145px;

  @media (min-width: $tablet-width) {
    order: 2;
    padding-right: 20px;
    flex: 1;
    background-color: transparent;
    padding-top: 0;

    .site-list__item--link {
      padding-top: 40px;
      padding-left: 5px;
    }
  }

  @media (min-width: $desktop-width) {
    flex: 0.7;
    order: 1;
  }
}

.site-list__offer {
  display: none;
  list-style: none;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    position: absolute;
    top: 103%;
    left: 2%;
  }
}

.site-list__offer--item {
  padding-right: 10px;
  font-weight: bold;
  font-size: 15px;
  line-height: 24px;
}

.site-list__offer--item a {
  color: black;
}
